import React from 'react'
import { Link } from 'react-router-dom'

function Header() {
  return (
    <header id="header" className="header d-flex align-items-center fixed-top">
      <div className="container-fluid d-flex align-items-center justify-content-between">
  
        <Link to="/" className="logo d-flex align-items-center  me-auto me-lg-0">
          <img src="assets/img/logo.png" alt="" /> 
          <i className="bi bi-camera"></i>
          <h1>PhotoFolio</h1>
        </Link>
  
        <nav id="navbar" className="navbar">
          <ul>
            <li><Link to="/" className="active">Home</Link></li>
            <li className="dropdown"><Link to="/services"><span>Services</span> <i className="bi bi-chevron-down dropdown-indicator"></i></Link>
              <ul>
                <li><a href="/">Web design</a></li>
                <li><a href="/">SEO</a></li>
                <li><a href="/">Graphice</a></li>
              </ul>
            </li>
            <li><Link to="/portfolio">Portfolio</Link></li>
            <li><Link to="/about">About</Link></li>
            <li><Link to="/contact">Contact</Link></li>
          </ul>
        </nav>
  
        <div className="header-social-links">
          <a href="/" className="twitter"><i className="bi bi-twitter"></i></a>
          <a href="/" className="facebook"><i className="bi bi-facebook"></i></a>
          <a href="/" className="instagram"><i className="bi bi-instagram"></i></a>
          <a href="/" className="linkedin"><i className="bi bi-linkedin"></i></a>
        </div>
        <i className="mobile-nav-toggle mobile-nav-show bi bi-list"></i>
        <i className="mobile-nav-toggle mobile-nav-hide d-none bi bi-x"></i>
  
      </div>
    </header>
  )
}

export default Header

